
Template.body.addContent((function() {
  var view = this;
  return [ HTML.Raw("<h1>Reactive Docker Status for Meteor</h1>\n  "), Spacebars.include(view.lookupTemplate("status")) ];
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("status");
Template["status"] = new Template("Template.status", (function() {
  var view = this;
  return [ HTML.Raw("<h2>Docker Containers</h2>\n  "), Blaze.Each(function() {
    return {
      _sequence: Spacebars.call(view.lookup("dockerContainers")),
      _variable: "container"
    };
  }, function() {
    return [ "\n    ", HTML.H3("Container #", Blaze.View("lookup:@index", function() {
      return Spacebars.mustache(view.lookup("@index"));
    })), "\n    ", HTML.H4(Blaze.View("lookup:dockerPrint", function() {
      return Spacebars.mustache(view.lookup("dockerPrint"), view.lookup("container"));
    })), "\n  " ];
  }) ];
}));
